import { products } from '@/data/products';
import { notFound } from 'next/navigation';
import { AudioPlayer } from '@/components/AudioPlayer';
import { AddToCartButton } from '@/components/AddToCartButton';
import { CheckCircle, Volume2, ShieldCheck, Truck, Star } from 'lucide-react';
import Link from 'next/link';
import { Metadata } from 'next';

// 1. Static Params Generation (SSG)
export async function generateStaticParams() {
    return products.map((product) => ({
        slug: product.id,
    }));
}

// 2. Dynamic Metadata
export async function generateMetadata({ params }: { params: { slug: string } }): Promise<Metadata> {
    const { slug } = await params;
    const product = products.find((p) => p.id === slug);

    if (!product) {
        return { title: 'Produit Non Trouvé' };
    }

    return {
        title: `${product.name} - ${product.tagline} | Santé Vitalité`,
        description: product.description,
        openGraph: {
            title: `${product.name} - Solution Naturelle`,
            description: product.description,
            images: [product.image],
        },
    };
}

export default async function ProductPage({ params }: { params: { slug: string } }) {
    const { slug } = await params;
    const product = products.find((p) => p.id === slug);

    if (!product) {
        notFound();
    }

    // Dynamic Color Logic
    const isBioActif = product.id === 'bioactif';
    const accentColor = isBioActif ? 'text-red-600' : 'text-green-600';
    const bgAccent = isBioActif ? 'bg-red-600' : 'bg-green-600';
    const borderAccent = isBioActif ? 'border-red-600' : 'border-green-600';

    // 3. JSON-LD Structured Data
    const jsonLd = {
        '@context': 'https://schema.org',
        '@type': 'Product',
        name: product.name,
        image: product.image,
        description: product.description,
        offers: {
            '@type': 'Offer',
            price: product.price,
            priceCurrency: 'XOF',
            availability: 'https://schema.org/InStock',
        },
    };

    return (
        <div className="bg-white min-h-screen pb-16 text-black">
            <script
                type="application/ld+json"
                dangerouslySetInnerHTML={{ __html: JSON.stringify(jsonLd) }}
            />

            {/* Breadcrumb */}
            <div className="bg-white border-b border-black/5">
                <div className="max-w-7xl mx-auto px-4 py-4 text-sm text-black/60">
                    <Link href="/" className="hover:text-black">Accueil</Link>
                    <span className="mx-2">/</span>
                    <Link href="/produits" className="hover:text-black">Produits</Link>
                    <span className="mx-2">/</span>
                    <span className={`font-semibold ${accentColor}`}>{product.name}</span>
                </div>
            </div>

            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
                <div className="lg:grid lg:grid-cols-2 lg:gap-x-12 lg:items-start">

                    {/* Image Gallery */}
                    <div className="flex flex-col-reverse">
                        <div className="aspect-w-1 aspect-h-1 w-full rounded-sm overflow-hidden border border-black/10 p-8 flex items-center justify-center bg-white">
                            <img
                                src={product.image}
                                alt={product.name}
                                className="w-full h-full object-contain hover:scale-105 transition-transform duration-500"
                            />
                        </div>
                        {/* Thumbnails could go here */}
                    </div>

                    {/* Product Info */}
                    <div className="mt-10 px-4 sm:px-0 sm:mt-16 lg:mt-0">
                        <div className="mb-6">
                            <span className={`inline-block py-1 px-3 rounded-full text-xs font-bold uppercase tracking-wide mb-2 border ${borderAccent} ${accentColor} bg-white`}>
                                {product.category}
                            </span>
                            <h1 className={`text-4xl font-extrabold tracking-tight mb-2 ${accentColor}`}>{product.name}</h1>
                            <div className="flex items-center text-sm text-black/40 mb-4">
                                <div className="flex text-black/80 mr-2">
                                    {[...Array(5)].map((_, i) => <Star key={i} className="w-4 h-4 fill-current stroke-none" />)}
                                </div>
                                <span className="font-mono tracking-widest uppercase text-xs">(128 avis vérifiés)</span>
                            </div>
                            <p className="text-3xl font-bold text-black font-mono">{product.price.toLocaleString()} FCFA</p>
                        </div>

                        <div className="prose prose-sm text-black/70 mb-8 font-light leading-relaxed">
                            <p>{product.description}</p>
                        </div>

                        {/* Benefits */}
                        <div className="mb-8">
                            <h3 className="text-sm font-medium text-black mb-4 uppercase tracking-widest border-b border-black/5 pb-2">Bienfaits Principaux</h3>
                            <ul className="space-y-3">
                                {product.benefits.map((benefit, i) => (
                                    <li key={i} className="flex items-center text-black/80">
                                        <CheckCircle className={`w-5 h-5 ${accentColor} mr-3 flex-shrink-0 stroke-[1.5]`} />
                                        {benefit}
                                    </li>
                                ))}
                            </ul>
                        </div>

                        {/* Audio Testimonials */}
                        <div className="bg-black/5 p-6 rounded-sm border border-black/10 mb-8">
                            <h4 className="text-black font-bold mb-4 flex items-center gap-2 text-sm uppercase tracking-widest">
                                <Volume2 className="w-4 h-4" /> Écoutez nos clients :
                            </h4>
                            <div className="space-y-3">
                                {product.testimonials.map(testimonial => (
                                    <AudioPlayer key={testimonial.id} testimonial={testimonial} colorClass="bg-white text-black border border-black/10" />
                                ))}
                            </div>
                        </div>

                        <div className="flex flex-col gap-4">
                            <AddToCartButton product={product} className="w-full justify-center bg-black hover:bg-black/90 text-white py-4 text-lg rounded-full shadow-lg" />
                            <p className="text-center text-xs text-black/40 flex items-center justify-center gap-2 font-mono uppercase tracking-widest mt-2">
                                <Truck className="w-4 h-4" /> Livraison 24H
                                <span className="mx-2">•</span>
                                <ShieldCheck className="w-4 h-4" /> Paiement à la livraison
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
